/**
 * ÎÇ†Ïßú ÌòïÏãùÏúºÎ°ú Ìè¨Îß∑ÌåÖ
 */
export const formatDate = (dateString: string): string => {
  try {
    const date = new Date(dateString)
    if (isNaN(date.getTime())) {
      throw new Error('Invalid date string')
    }
    return date.toLocaleDateString('ko-KR', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    })
  } catch (error) {
    console.error('ÎÇ†Ïßú Ìè¨Îß∑ÌåÖ Ïò§Î•ò:', error)
    return 'ÎÇ†Ïßú Ïò§Î•ò'
  }
}

/**
 * ÎÇ†ÏßúÏôÄ ÏãúÍ∞Ñ ÌòïÏãùÏúºÎ°ú Ìè¨Îß∑ÌåÖ
 */
export const formatDateTime = (dateString: string): string => {
  try {
    const date = new Date(dateString)
    if (isNaN(date.getTime())) {
      throw new Error('Invalid date string')
    }
    return date.toLocaleString('ko-KR', {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    })
  } catch (error) {
    console.error('ÎÇ†ÏßúÏãúÍ∞Ñ Ìè¨Îß∑ÌåÖ Ïò§Î•ò:', error)
    return 'ÎÇ†ÏßúÏãúÍ∞Ñ Ïò§Î•ò'
  }
}

/**
 * ÏïàÏ†ÑÌïú Ïà´Ïûê Î≥ÄÌôò
 */
export const safeParseInt = (value: string | number, defaultValue: number = 0): number => {
  try {
    if (typeof value === 'number') return value
    const parsed = parseInt(value, 10)
    return isNaN(parsed) ? defaultValue : parsed
  } catch (error) {
    console.error('Ï†ïÏàò Î≥ÄÌôò Ïò§Î•ò:', error)
    return defaultValue
  }
}

/**
 * ÏïàÏ†ÑÌïú Ïã§Ïàò Î≥ÄÌôò
 */
export const safeParseFloat = (value: string | number, defaultValue: number = 0): number => {
  try {
    if (typeof value === 'number') return value
    const parsed = parseFloat(value)
    return isNaN(parsed) ? defaultValue : parsed
  } catch (error) {
    console.error('Ïã§Ïàò Î≥ÄÌôò Ïò§Î•ò:', error)
    return defaultValue
  }
}

/**
 * ÏûÖÎ†•Í∞í Í≤ÄÏ¶ù
 */
export const validateInput = {
  required: (value: string): boolean => value.trim().length > 0,
  minLength: (value: string, min: number): boolean => value.trim().length >= min,
  maxLength: (value: string, max: number): boolean => value.trim().length <= max,
  email: (value: string): boolean => /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(value),
  phone: (value: string): boolean => /^[0-9-+\s()]+$/.test(value),
  number: (value: string): boolean => !isNaN(Number(value)) && value.trim() !== '',
  positiveNumber: (value: string): boolean => {
    const num = Number(value)
    return !isNaN(num) && num > 0
  }
}

/**
 * ÏóêÎü¨ Î°úÍπÖ
 */
export const logError = (context: string, error: unknown, additionalInfo?: Record<string, unknown>): void => {
  const errorInfo = {
    timestamp: new Date().toISOString(),
    context,
    error: error instanceof Error ? {
      message: error.message,
      stack: error.stack,
      name: error.name
    } : error,
    additionalInfo,
    userAgent: typeof window !== 'undefined' ? window.navigator.userAgent : 'Server',
    url: typeof window !== 'undefined' ? window.location.href : 'Unknown'
  }
  
  console.error('üö® ÏóêÎü¨ Î∞úÏÉù:', errorInfo)
  
  // Ïã§Ï†ú ÌîÑÎ°úÎçïÏÖòÏóêÏÑúÎäî ÏóêÎü¨ Ï∂îÏ†Å ÏÑúÎπÑÏä§Î°ú Ï†ÑÏÜ°
  // Ïòà: Sentry, LogRocket Îì±
  if (process.env.NODE_ENV === 'production') {
    // ÏóêÎü¨ Ï∂îÏ†Å ÏÑúÎπÑÏä§ Ï†ÑÏÜ° Î°úÏßÅ
    console.log('ÌîÑÎ°úÎçïÏÖò ÌôòÍ≤Ω: ÏóêÎü¨ Ï∂îÏ†Å ÏÑúÎπÑÏä§Î°ú Ï†ÑÏÜ°Îê®')
  }
}

/**
 * ÏÑ±Îä• Ï∏°Ï†ï
 */
export const measurePerformance = <T>(name: string, fn: () => T): T => {
  const start = performance.now()
  try {
    const result = fn()
    const end = performance.now()
    console.log(`‚è±Ô∏è ${name} Ïã§Ìñâ ÏãúÍ∞Ñ: ${(end - start).toFixed(2)}ms`)
    return result
  } catch (error) {
    const end = performance.now()
    console.error(`‚ùå ${name} Ïã§Ìñâ Ïã§Ìå® (${(end - start).toFixed(2)}ms):`, error)
    throw error
  }
}

/**
 * ÎπÑÎèôÍ∏∞ ÏÑ±Îä• Ï∏°Ï†ï
 */
export const measureAsyncPerformance = async <T>(name: string, fn: () => Promise<T>): Promise<T> => {
  const start = performance.now()
  try {
    const result = await fn()
    const end = performance.now()
    console.log(`‚è±Ô∏è ${name} Ïã§Ìñâ ÏãúÍ∞Ñ: ${(end - start).toFixed(2)}ms`)
    return result
  } catch (error) {
    const end = performance.now()
    console.error(`‚ùå ${name} Ïã§Ìñâ Ïã§Ìå® (${(end - start).toFixed(2)}ms):`, error)
    throw error
  }
}

/**
 * ÎîîÎ∞îÏö¥Ïä§ Ìï®Ïàò
 */
export const debounce = <T extends (...args: any[]) => any>(
  func: T,
  wait: number
): ((...args: Parameters<T>) => void) => {
  let timeout: NodeJS.Timeout | null = null
  
  return (...args: Parameters<T>) => {
    if (timeout) clearTimeout(timeout)
    timeout = setTimeout(() => func(...args), wait)
  }
}

/**
 * Ïä§Î°úÌãÄ Ìï®Ïàò
 */
export const throttle = <T extends (...args: any[]) => any>(
  func: T,
  limit: number
): ((...args: Parameters<T>) => void) => {
  let inThrottle: boolean = false
  
  return (...args: Parameters<T>) => {
    if (!inThrottle) {
      func(...args)
      inThrottle = true
      setTimeout(() => inThrottle = false, limit)
    }
  }
}

/**
 * Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄ ÏïàÏ†ÑÌïú Ï†ëÍ∑º
 */
export const safeLocalStorage = {
  get: (key: string): string | null => {
    try {
      return localStorage.getItem(key)
    } catch (error) {
      logError('localStorage Ï†ëÍ∑º Ïò§Î•ò', error, { key })
      return null
    }
  },
  
  set: (key: string, value: string): boolean => {
    try {
      localStorage.setItem(key, value)
      return true
    } catch (error) {
      logError('localStorage Ï†ÄÏû• Ïò§Î•ò', error, { key, value })
      return false
    }
  },
  
  remove: (key: string): boolean => {
    try {
      localStorage.removeItem(key)
      return true
    } catch (error) {
      logError('localStorage ÏÇ≠Ï†ú Ïò§Î•ò', error, { key })
      return false
    }
  },
  
  clear: (): boolean => {
    try {
      localStorage.clear()
      return true
    } catch (error) {
      logError('localStorage Ï¥àÍ∏∞Ìôî Ïò§Î•ò', error)
      return false
    }
  }
} 